

.side_navbar_menu {
 background-color: #776777 ;
  width:  250px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 850ms;
}

.side_navbar_menu.active {
  left: 0;
  transition: 350ms;
  background-color: #9b659bc2;
}

.side_navbar_text {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
}

.side_navbar_text a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}

.side_navbar_text a:hover {
  background-color: #703e70 ;
}

.side_navbar_list {
  width: 100%;
}

.side_navbar_toggler {
  background-color: #917091;
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: start;
  align-items: center;
}

span {
  margin-left: 16px;
}

.app_bar {
  margin-left: 250px;
  width: calc(100% - 250px)!important;
  transition:margin-left 0.5s;
  padding:20px;
  overflow:hidden;
  width:100%;
}


@media(max-width:568px){
  .side_navbar_container{display:none}
}
