:root {
  --font-header: 'Nova Mono', monospace;
  --font-text: 'Open Sans', sans-serif;
  --color-theme: #F1EEDB;
  --color-bg: #282B24;

  --animation-sentence: 'You know it\'s not the page you are looking for, right?';
  --animation-duration: 40s;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  width: 100%;
  font-family: 'Open Sans', sans-serif;
  font-family: var(--font-text);
  color: #F1EEDB;
  color: var(--color-theme);
  background: #282B24;
  background: var(--color-bg);
  overflow: hidden;
}
.container {
  text-align: center;
  margin: 1rem 0.5rem 0;
}
.container h1 {
  font-family: 'Nova Mono', monospace;
  font-family: var(--font-header);
  font-size: calc(4rem + 2vw);
	text-transform: uppercase;
}
.container p {
  text-transform: uppercase;
  letter-spacing: 0.2rem;
  font-size: 2rem;
  margin: 1.5rem 0 3rem;
}
svg.keyhole {
  height: 82px;
  width: 82px;
  opacity: 0;
  visibility: hidden;
  /* define an animation for the keyhole, to introduce it
  paused by default, run with a timeout in JavaScript
  */
  -webkit-animation: showKey 0.5s 0.5s paused ease-out forwards;
          animation: showKey 0.5s 0.5s paused ease-out forwards;
}
svg.key {
  height: 164px;
  width: 164px;
  position: absolute;
  opacity: 0;
  visibility: hidden;
  /* define an animation for the keyhole, to introduce it
  paused by default, run with a timeout in JavaScript
  */
  -webkit-animation: showKey 0.5s 0.5s paused ease-out forwards;
          animation: showKey 0.5s 0.5s paused ease-out forwards;
}
.ghost {
  /* border: 1px solid tomato; */
  position: absolute;
  bottom: 5px;
  left: calc(50% - 100px);
  width: 200px;
  height: 200px;
  /* have the ghost move to the right and to the left of the screen, turning to its central position and repeating the animation twice */
  -webkit-animation: hoverGhost calc(40s/2) ease-in-out 2;
          animation: hoverGhost calc(40s/2) ease-in-out 2;
  -webkit-animation: hoverGhost calc(var(--animation-duration)/2) ease-in-out 2;
          animation: hoverGhost calc(var(--animation-duration)/2) ease-in-out 2;

}
/* introduce text through a pseudo element, connected to the animated div */
.ghost:before {
  content: 'You know it\'s not the page you are looking for, right?';
  content: var(--animation-sentence);
  color: #F1EEDB;
  color: var(--color-theme);
  border-radius: 50%;
  position: absolute;
  bottom: 100%;
  text-align: center;
  line-height: 2;
  padding: 1rem;
  visibility: hidden;
  opacity: 0;
  
  -webkit-animation:
    showText calc(40s/8) calc(40s*3/16) ease-out forwards,
    showNewText calc(40s/8) calc(40s*27/40) ease-out forwards,
    showFinalText calc(40s/8) 40s ease-out forwards;
  
          animation:
    showText calc(40s/8) calc(40s*3/16) ease-out forwards,
    showNewText calc(40s/8) calc(40s*27/40) ease-out forwards,
    showFinalText calc(40s/8) 40s ease-out forwards;
  
  -webkit-animation:
    showText calc(var(--animation-duration)/8) calc(var(--animation-duration)*3/16) ease-out forwards,
    showNewText calc(var(--animation-duration)/8) calc(var(--animation-duration)*27/40) ease-out forwards,
    showFinalText calc(var(--animation-duration)/8) var(--animation-duration) ease-out forwards;
  
          animation:
    showText calc(var(--animation-duration)/8) calc(var(--animation-duration)*3/16) ease-out forwards,
    showNewText calc(var(--animation-duration)/8) calc(var(--animation-duration)*27/40) ease-out forwards,
    showFinalText calc(var(--animation-duration)/8) var(--animation-duration) ease-out forwards;

}

@-webkit-keyframes hoverGhost {
  25% {
    -webkit-transform: translateX(20vw);
            transform: translateX(20vw);
  }
  75% {
    -webkit-transform: translateX(-20vw);
            transform: translateX(-20vw);
  }
}

@keyframes hoverGhost {
  25% {
    -webkit-transform: translateX(20vw);
            transform: translateX(20vw);
  }
  75% {
    -webkit-transform: translateX(-20vw);
            transform: translateX(-20vw);
  }
}

@-webkit-keyframes showKey {
  to {
    opacity: 1;
    visibility: visible;
  }
}

@keyframes showKey {
  to {
    opacity: 1;
    visibility: visible;
  }
}


